<template>
  <form class="section" @submit.prevent="submit">
    <div v-if="questionSection" class="title directed aligned">
      <label><strong>   </strong></label>
      <textarea
        v-if="!questionSection.type.includes('data')"
        class="form-control"
        v-model="questionSection.type"
        type="text"
        placeholder="Question address"
      ></textarea>
      <img
        v-if="questionSection.type.includes('http')"
        sizes="(min-width: 30em) 30em, 100vw"
        :src="questionSection.type"
      />
      <div class="upload-photo">
        <input
          ref="upload_title"
          style="display: none"
          type="file"
          @change="
            (e) => {
              uploadPhoto(e, 'address');
            }
          "
        />
        <div class="upload_icon">
          <i @click="$refs['upload_title'].click()" class="fa fa-upload"></i>
        </div>
      </div>
      <hr />
      <div class="row directed">
        <div class="col-md-6">
          <label><strong> </strong></label>
          <input
            style="margin-left: 13px"
            class="form-control"
            v-model.number="numberOfQuestions"
            type="number"
            min="1"
            max="40"
          />
        </div>
        <div class="col-md-6">
          <label><strong> </strong></label>
          <select class="form-control" v-model="type">
            <option :value="0"></option>
            <option :value="1"></option>
          </select>
        </div>
      </div>
    </div>
    <div v-if="questionSection.questions.length > 0" calss="questions">
      <div
        v-for="(question, i) in questionSection.questions"
        :key="i"
        class="question directed aligned"
      >
        <div class="row">
          <div class="col-md-10">
            <label><strong></strong></label>
            <v-textarea
              v-model="question['question']"
              label="The Question"
              outlined
              rows="3"
              row-height="25"
              shaped
            ></v-textarea>
            <img
              class="img-fluid"
              v-if="question['question'].includes('http')"
              :src="question['question']"
            />

            <div class="upload-image">
              <input
                style="display: none"
                :id="'sec' + random + 'ques' + i"
                type="file"
                @change="
                  (e) => {
                    uploadPhoto(e, i);
                  }
                "
              />
              <div class="upload_icon">
                <i @click="pickImage(i)" class="fa fa-upload"></i>
              </div>
            </div>
          </div>
          <div class="col-md-2">
            <div>
              <label><strong> </strong></label>
              <input
                class="form-control"
                v-model.number="question['fullDegree']"
                type="number"
                :placeholder="'full degree'"
              />
            </div>

            <div v-if="type == 1">
              <label><strong> </strong></label>
              <input
                class="form-control"
                @change="(e) => changeQuestionChoices(e, i)"
                type="number"
                min="2"
                max="6"
                :value="question.choices.length"
              />
            </div>
          </div>
          <hr />
        </div>
        <div v-if="type === 1" class="row">
          <div class="col-md-3" v-for="(ch, c) in question.choices" :key="c">
            <input
              class="form-control"
              v-model="question.choices[c]"
              type="text"
              :placeholder="'Choice' + (c + 1)"
            />
          </div>
        </div>
      </div>
    </div>
  </form>
</template>
<script>
import { eventBus } from "../../main.js";
export default {
  props: ["section", "index"],
  mounted() {
    this.random = Math.random() * 1000;
    if (Object.keys(this.section).length > 0) {
      this.questionSection = this.section;
      if (this.questionSection.questions[0]?.choices?.length > 0) this.type = 1;
      else this.type = 0;
      this.numberOfQuestions = this.questionSection?.questions?.length;
    } else {
      this.numberOfQuestions = 1;
    }
    eventBus.$on("collectQuestions", () => {
      this.doSubmit();
    });
    eventBus.$on("save", () => {
      this.save();
    });
  },
  data() {
    return {
      type: 1,
      numberOfQuestions: 0,
      questionSection: {
        number: 0,
        type: "",
        mimetype: "",
        questions: [],
      },
      random: 123,
    };
  },
  computed: {},
  watch: {
    numberOfQuestions(val) {
      this.setQuestions(val);
    },
    type(val) {
      this.setQuestions(val);
    },
    section(val) {
      this.questionSection = val;
      if (this.questionSection.questions[0]?.choices.length > 0) this.type = 1;
      this.numberOfQuestions = this.questionSection?.questions?.length;
    },
  },
  methods: {
    doSubmit() {
      console.log(this.index);
      this.$emit("concatArraysOfQuestions", this.questionSection, this.index);
    },
    save() {
      this.$emit("concatArraysOfQuestions", this.questionSection, this.index);
    },

    uploadPhoto(e, field) {
      const allowed = ["jpg", "jpeg", "png", "svg"];
      const file = event.target.files[0];
      if (!allowed.includes(file.type.split("/")[1])) {
        alert("      ");
        return;
      }
      if (file.size > 300000) {
        alert("     ");
        return;
      }
      let image = URL.createObjectURL(file);
      if (field === "address") {
        this.questionSection.type = image;
        this.questionSection.mimetype = file.type;
      } else {
        this.questionSection.questions[field]["question"] = image;
        this.questionSection.questions[field].mimetype = file.type;
      }
      // };
    },
    pickImage(i) {
      document.getElementById("sec" + this.random + "ques" + i).click();
    },
    changeQuestionChoices(e, i) {
      const noc = e.target.value;
      const question = this.questionSection.questions[i];
      const choices = [];
      for (let c = 0; c < noc; c++) {
        choices.push(question?.choices[c] || "");
      }
      this.questionSection.questions[i].choices = choices;
    },
    setQuestions(length) {
      const section = { ...this.questionSection };
      const newQuestions = [];
      const questions = section.questions;
      for (let i = 0; i < length; i++) {
        const question = questions[i];
        newQuestions[i] = {
          number: question?.number || i,
          question: question?.question || "",
          fullDegree: question?.fullDegree || 1,
          mimetype: question?.mimetype || "",
          choices: [],
        };
        if (this.type == 1) {
          if (question?.choices.length > 0) {
            newQuestions[i]["choices"] = question.choices;
          } else {
            newQuestions[i]["choices"] = ["", "", "", ""];
          }
        }
      }
      console.log(newQuestions);
      this.questionSection = { ...section, questions: newQuestions };
    },
  },
};
</script>
